.abonements {
  background-color: #F8F9FC;
  padding: 110px 0;
}

.abonements__wrapper {
  flex-direction: column;
  align-items: center;
  position: relative;

  h2 {
    font-family: "Bebas Neue", "Open Sans", sans-serif;
    font-size: 49px;
    line-height: 49px;
    letter-spacing: 0.03em;
    color: #102654;
    margin: 0;
    margin-bottom: 35px;
  }
}

.abonements__wrapper::after {
  content: url("../img/content/weight-desktop.png");
  position: absolute;
  right: -100px;
  bottom: -260px;
  z-index: 0;
}

.abonements__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 340px;
  list-style: none;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);
  padding: 0;
  margin: 0;
  margin-bottom: 40px;

  button {
    min-height: 50px;
    border: none;
    background-color: inherit;
    font-weight: normal;
    font-size: 20px;
    line-height: 150%;
    color: rgba(16, 38, 84, 0.5);
    padding: 0;
    position: relative;
  }

  .abonements__active-item {
    color: #102654;
  }

  .abonements__active-item::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #D10A42;
    bottom: -2px;
    left: 0;
  }
}

.abonements__cards {
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 1;
}

.abonements__card {
  background-color: #ffffff;
  width: 30.9%;
  min-height: 448px;
  padding: 68px 77px;
  text-align: center;
  box-sizing: border-box;

  h3 {
    font-weight: bold;
    font-size: 26px;
    line-height: 29px;
    color: #102654;
    margin: 0;
    margin-bottom: 15px;
  }

  > span {
    display: inline-block;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #495A7D;
    margin-bottom: 85px;
  }

  p {
    background-color: inherit;
    font-weight: bold;
    font-size: 60px;
    line-height: 150%;
    color: #103AAE;
    margin: 0;
    margin-bottom: 40px;
    position: relative;
    z-index: 1;
  }

  p::before {
    content: "";
    width: 203px;
    height: 69px;
    font-weight: bold;
    font-size: 100px;
    line-height: 150%;
    color: #E8E8E8;
    opacity: 0.48;
    position: absolute;
    z-index: -1;
    left: 0;
    top: -60px;
  }

  span {
    color: #102654;
  }

  a {
    color: #102654;
    border: 2px solid #102654;
  }
}

.abonements__card--active {
  border: 7px solid red;

  a {
    color: #ffffff;
    background-image: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
    border: none;
  }
}

.abonements__card--5000 p::before {
  content: "5000";
}

.abonements__card--1700 p::before {
  content: "1700";
}

.abonements__card--2700 p::before {
  content: "2700";
}

@media (max-width: $mid-size) {
  .abonements {
    padding: 90px 0;
  }

  .abonements__wrapper h2 {
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 28px;
  }

  .abonements__wrapper::after {
    content: url("../img/content/weight-tablet.png");
    right: -45px;
    bottom: -220px;
  }

  .abonements__menu {

    width: 332px;
    margin-bottom: 45px;

    button {
      font-size: 18px;
    }
  }

  .abonements__cards {
    flex-direction: column;
  }

  .abonements__card {
    width: 442px;
    padding: 68px 118px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  .abonements__card--active {
    background-color: #F8F9FC;
  }
}

@media (max-width: $min-size) {
  .abonements {
    padding: 70px 0;
  }

  .abonements__wrapper h2 {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 28px;
  }

  .abonements__wrapper::after {
    content: none;
  }

  .abonements__menu {
    width: 290px;
    margin-bottom: 35px;

    button {
      font-size: 18px;
    }
  }

  .abonements__card {
    width: 290px;
    padding: 55px 42px;
    margin-bottom: 35px;

    h3 {
      font-size: 22px;
    }

    > span {
      margin-bottom: 70px;
    }

    p {
      font-size: 48px;
      margin-bottom: 30px;
    }

    p::before {
      font-size: 80px;
    }
  }

  .abonements__card--active {
    background-color: #ffffff;
  }
}
